type GetChatCreatedURLParamsProps = {
  chatId?: string
  hashParams: Record<string, string>
  locationSearch: string
}

function getChatCreatedURLParams({
  chatId,
  hashParams,
  locationSearch,
}: GetChatCreatedURLParamsProps): string {
  const searchParams = new URLSearchParams(locationSearch)

  if (chatId) {
    searchParams.set("chatId", chatId)
  } else {
    searchParams.delete("chatId")
  }

  const hashParamsListString = Object.entries(hashParams).reduce(
    (acc: string, [key, value]: [string, string]) => {
      const newAcc = acc + `${acc ? "&" : ""}${key}=${value}`
      return newAcc
    },
    "",
  )
  const hashParamsURLString = hashParamsListString ? `#?${hashParamsListString}` : ""
  const urlParams = `?${searchParams.toString()}${hashParamsURLString}`

  return urlParams
}

export { getChatCreatedURLParams }
